.projects {
  position: relative;
  overflow: hidden; }
  .projects .hexGrid {
    display: flex;
    flex-wrap: wrap;
    width: 120vw;
    margin: 0 0;
    overflow: hidden;
    font-size: 15px;
    list-style-type: none;
    margin-left: calc(-50% + 35vw);
    padding: 0;
    padding-bottom: 2.5vw;
    z-index: 1; }
  .projects .title {
    width: 100%;
    top: 50%;
    height: 12px;
    z-index: 5; }
  .projects .hex {
    width: 10vw;
    /* = 100 / 15 */
    -webkit-transition: all 0.4s ease-out;
    transition: all 0.4s ease-out; }
  .projects .hex:nth-child(23n+13) {
    /* first hexagon of even rows */
    margin-left: 5vw;
    /* = width of .hex / 2  to indent even rows */ }
  .projects .hexIn {
    position: absolute;
    width: 96%;
    padding-bottom: 110.851%;
    /* =  width / sin(60) */
    margin: 0 2%;
    overflow: hidden;
    visibility: hidden;
    outline: 1px solid transparent;
    /* fix for jagged edges in FF on hover transition */
    -webkit-transform: rotate3d(0, 0, 1, -60deg) skewY(30deg);
            transform: rotate3d(0, 0, 1, -60deg) skewY(30deg);
    background: grey; }
  .projects .hex {
    position: relative;
    visibility: hidden;
    outline: 1px solid transparent;
    /* fix for jagged edges in FF on hover transition */ }
  .projects .hex::after {
    content: '';
    display: block;
    padding-bottom: 86.602%;
    /* =  100 / tan(60) * 1.5 */ }
  .projects .hexIn * {
    position: absolute;
    visibility: visible;
    outline: 1px solid transparent;
    /* fix for jagged edges in FF on hover transition */ }
  .projects .hexLink, .projects .hexDud {
    display: block;
    width: 100%;
    height: 100%;
    text-align: center;
    color: #fff;
    overflow: hidden;
    -webkit-transform: skewY(-30deg) rotate3d(0, 0, 1, 60deg);
            transform: skewY(-30deg) rotate3d(0, 0, 1, 60deg); }
  .projects .hexDud {
    background: #202020; }
  .projects .hex img {
    left: -100%;
    right: -100%;
    width: auto;
    height: 100%;
    margin: 0 auto;
    -webkit-transform: rotate3d(0, 0, 0, 0deg);
    transform: rotate3d(0, 0, 0, 0deg); }
  .projects .hexGrid.active .hex {
    opacity: 0.8; }
  .projects .hexGrid.active .hex.hover {
    opacity: 1; }

